.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  font-size: 16px;
  padding: 8px 20px;
  border: 1px solid $light;
  color: $light;

  @include hover($speed, $time-function, background, decor) {
    background: $light;
    color: $decor;
  }
}